@import url('https://fonts.googleapis.com/css2?family=Titillium+Web:ital,wght@0,300;0,400;0,600;1,300;1,400;1,600&display=swap');

* {
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

.selectors{
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  overflow-x: hidden;
  overflow-y: scroll;
  font-family: 'Titillium Web', sans-serif;
  border-radius: 25px;
  margin: 5px;
  box-shadow: 0px 0px 10px #00000079;
  gap: 1.3vh;
  padding-bottom: 3vh;
  
}

.domSelector {
  width: 50vw;
  max-width: 600px;
  min-width: 200px;


  /* height: calc(70vh + 30vw); */
  /* min-height: calc(50 * 3); */

  height: 70vh;

  position: fixed;

  /* top:50%;
  left:20px; */
  top: 0px;
  left: 0px;
  z-index: 99999;

  transform: scale(1);

  background-color: #ffffff17;
  backdrop-filter: blur(33px);
  -webkit-backdrop-filter: blur(33px);
  color: #ffffff;
}
.domSelector2 {
  width: 50%;
  height: 50%;
  position: fixed;

  /* right: 20px;
  top:50%; */
  bottom: 0px;
  right: 0px;
  z-index: 99999;

  transform: scale(1);

}
.hidden {
  width: 0.001px;
  height: 0.001px;
  transform: scale(0.00001);
}
.ballerAnim {
  transition: 
    width 0.7s cubic-bezier(.05,.18,.17,.96),
    height 0.7s cubic-bezier(.05,.18,.17,.96),
    transform 0.7s cubic-bezier(.05,.18,.17,.96);
}

.ballerAnim> .row{
  transition: 
    opacity 0.7s cubic-bezier(1,.13,.54,1);
  opacity: 1;
}

.ballerAnim.hidden> .row{
  transform:rotateX('90deg');
  opacity: 0.001;
}

.row{
  display: flex;
  justify-content: center;
  padding: 0px 2vw;
}

h1 {
  opacity: 0.7;
  letter-spacing: 1px;
  /* font-size: calc(2vw * 1.2vh); */
  font-size: min(7vw, 5vh);
  font-weight: 600;
}

h2 {
  opacity: 0.5;
  font-size: min(4vw, 3vh);
  font-weight: 300;
  font-style: italic;
}

article {
  opacity: 0.35;
  font-size: 16px;
  display: flex;
  flex-direction: column;
  gap: 1vh;
}

.visitSite {
  background-color: #00000096;
  color: #0a84ffcb;
  backdrop-filter: blur(33px);
  -webkit-backdrop-filter: blur(33px);
  padding: 0.6vh 20%;
  font-size: 20px;
  border-radius: 15px;
  text-decoration: none;
  font-weight: 600;
}



#modelViewer {
  height: 20vh;
}